/*
// animations
*/
@keyframes loader-animation {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}

/*
// base
*/
html {
  height: 100%;
}

body {
  height: 100%;
  background: #fafafa;
  color: #262626;
  font-family: "Roboto", "sans-serif";
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  overflow-y: scroll;
  backface-visibility: hidden;
}

[dir=ltr] body {
  text-align: left;
  direction: ltr;
}

[dir=rtl] body {
  text-align: right;
  direction: rtl;
}

a {
  color: #003f2b;
}

a:hover {
  color: #003f2b;
  text-decoration: none;
}

label {
  margin-bottom: .25rem;
  font-size: 15px;
}

h1 {
  font-size: 40px;
  font-weight: 500;
}

h2 {
  font-size: 32px;
  font-weight: 500;
}

h3 {
  font-size: 28px;
  font-weight: 500;
}

h4 {
  font-size: 24px;
  font-weight: 500;
}

h5 {
  font-size: 20px;
  font-weight: 500;
}

h6 {
  font-size: 16px;
  font-weight: 500;
}

/*
// .document
*/
.document {
  max-width: 760px;
  margin: 0 auto;
}

.document__header {
  text-align: center;
  padding: 44px 0 52px;
}

.document__title {
  font-weight: 700;
  letter-spacing: .01em;
  margin: 0;
}

.document__subtitle {
  font-size: 15px;
  color: #999;
  margin-top: 8px;
  margin-bottom: -4px;
}

.document__content {
  padding: 50px;
}

.document__signature {
  margin-top: 2.5rem;
}

@media (max-width: 1199.98px) {
  .document__header {
    padding: 44px 0 48px;
  }
  .document__subtitle {
    margin-top: 4px;
  }
}

@media (max-width: 991.98px) {
  .document__content {
    padding: 40px;
  }
}

@media (max-width: 767.98px) {
  .document__header {
    padding: 36px 0 40px;
  }
  .document__title {
    font-size: 36px;
  }
  .document__content {
    padding: 28px;
  }
}

@media (max-width: 575.98px) {
  .document__title {
    font-size: 32px;
  }
  .document__subtitle {
    font-size: 14px;
  }
  .document__content {
    padding: 24px;
  }
  .document__content .typography {
    font-size: 15px;
  }
  .document__signature {
    margin-top: 24px;
  }
}

/*
// grid
*/
.col-1of5 {
  padding: 0 15px;
  flex: 0 0 20%;
  max-width: 20%;
  position: relative;
  width: 100%;
}

.col-2of5 {
  padding: 0 15px;
  flex: 0 0 40%;
  max-width: 40%;
  position: relative;
  width: 100%;
}

.col-3of5 {
  padding: 0 15px;
  flex: 0 0 60%;
  max-width: 60%;
  position: relative;
  width: 100%;
}

.col-4of5 {
  padding: 0 15px;
  flex: 0 0 80%;
  max-width: 80%;
  position: relative;
  width: 100%;
}

.col-5of5 {
  padding: 0 15px;
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
  width: 100%;
}

/*
// header
*/
@media (min-width: 1200px) {
  .mobile-header,
  .mobile-menu {
    display: none;
  }
}

@media (max-width: 1199.98px) {
  .header {
    display: none;
  }
}

/*
// .site
*/
.site {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.site__header {
  flex-shrink: 0;
}

.site__body {
  flex-grow: 1;
}

.site__footer {
  flex-shrink: 0;
}

