/*
// .alert
*/
.alert {
  border: none;
  border-radius: 2px;
  font-size: 15px;
  line-height: 20px;
  padding: .75rem 1rem;
}

.alert a {
  box-decoration-break: clone;
  -webkit-box-decoration-break: clone;
  color: currentColor;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 1.5px;
  padding: 0 3px;
}

.alert a:hover {
  background: rgba(255, 255, 255, 0.85);
}

.alert a:active {
  background: rgba(255, 255, 255, 0.5);
}

.alert hr {
  border-color: currentColor;
  opacity: .12;
}

.alert-primary {
  background: #b2d9ff;
  color: #36414d;
}

.alert-secondary {
  background: #d9d9d9;
  color: #4d4d4d;
}

.alert-info {
  background: #b8e2e5;
  color: #2d3e40;
}

.alert-success {
  background: #bde5a1;
  color: #34402d;
}

.alert-danger {
  background: #ffb8b8;
  color: #473333;
}

.alert-warning {
  background: #f2e291;
  color: #4d472e;
}

.alert-lg {
  font-size: 16px;
  padding: 1.125rem 1.375rem;
}

[dir=ltr] .alert-dismissible {
  padding-right: 3rem;
}

[dir=rtl] .alert-dismissible {
  padding-left: 3rem;
}

.alert-dismissible .close {
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  height: 100%;
  transition: all .2s;
  fill: rgba(0, 0, 0, 0.3);
  background: transparent;
}

[dir=ltr] .alert-dismissible .close {
  right: 0;
  border-top-right-radius: 1.5px;
  border-bottom-right-radius: 1.5px;
}

[dir=rtl] .alert-dismissible .close {
  left: 0;
  border-top-left-radius: 1.5px;
  border-bottom-left-radius: 1.5px;
}

.alert-dismissible .close:focus {
  outline: none;
}

.alert-dismissible .close:not(:disabled):not(.disabled):focus,
.alert-dismissible .close:not(:disabled):not(.disabled):hover {
  background: rgba(0, 0, 0, 0.08);
  fill: rgba(0, 0, 0, 0.5);
  opacity: 1;
}

/*
// .arrow
*/
.arrow__button {
  display: flex;
  position: relative;
  z-index: 0;
  align-items: center;
  border: none;
  background: transparent;
  pointer-events: none;
  transition: color .15s;
  color: #fff;
}

.arrow__button:before {
  background: #006847;
}

.arrow__button svg {
  fill: currentColor;
  display: block;
}

[dir=ltr] .arrow__button svg {
  transform: scaleX(1);
}

[dir=rtl] .arrow__button svg {
  transform: scaleX(-1);
}

.arrow__button:focus {
  outline: none;
}

.arrow__button:before {
  position: absolute;
  display: block;
  content: '';
  height: 100%;
  transform-origin: center center;
  z-index: -1;
  border-radius: 2px;
  pointer-events: auto;
  transition: background .15s;
}

.arrow__button:hover {
  color: #fff;
}

.arrow__button:hover:before {
  background: #333;
}

.arrow__button:active {
  color: #fff;
}

.arrow__button:active:before {
  background: #474747;
}

.arrow__button:active, .arrow__button:active:before {
  transition-duration: 0s;
}

.arrow__button:after {
  position: absolute;
  display: block;
  content: '';
  top: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  transition: background .2s;
  opacity: .01;
}

[dir=ltr] .arrow__button:after {
  left: -2px;
}

[dir=rtl] .arrow__button:after {
  right: -2px;
}

.arrow__button:hover:after {
  background: rgba(0, 0, 0, 0.01);
}

.arrow__button:active:after {
  background: rgba(255, 255, 255, 0.01);
  transition-duration: 0s;
}

[dir=ltr] .arrow--next svg {
  margin-right: -1px;
}

[dir=rtl] .arrow--next svg {
  margin-left: -1px;
}

[dir=ltr] .arrow--prev svg {
  margin-left: -1px;
}

[dir=rtl] .arrow--prev svg {
  margin-right: -1px;
}

.arrow__button {
  height: 23px;
  padding: 0 14.37131px;
}

.arrow__button:before {
  width: calc(100% - 8.37131px);
}

[dir=ltr] .arrow__button:before {
  left: 4.18566px;
}

[dir=rtl] .arrow__button:before {
  right: 4.18566px;
}

[dir=ltr] .arrow__button:before {
  transform: skewX(-20deg);
}

[dir=rtl] .arrow__button:before {
  transform: skewX(20deg);
}

/*
// .breadcrumb
*/
.breadcrumb {
  background: transparent;
  padding: 0;
  margin: 0;
  line-height: 20px;
  font-size: 14px;
  color: #6c757d;
}

.breadcrumb:after {
  content: '';
  clear: both;
}

.breadcrumb__list {
  display: block;
  list-style: none;
  padding: 0;
  margin: -4px 0 0;
  width: 100%;
}

.breadcrumb__item {
  margin-top: 4px;
}

[dir=ltr] .breadcrumb__item {
  margin-right: -7.12435px;
  float: left;
}

[dir=rtl] .breadcrumb__item {
  margin-left: -7.12435px;
  float: right;
}

.breadcrumb__item-link {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 0;
  height: 21px;
  padding: 0 15.12435px;
  pointer-events: none;
}

.breadcrumb__item-link, .breadcrumb__item-link:hover {
  color: inherit;
}

.breadcrumb__item-link:before, .breadcrumb__item-link:after {
  pointer-events: auto;
  position: absolute;
  background-color: #ebebeb;
  top: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 3px;
  transition: background .1s;
}

.breadcrumb__item-link:before {
  content: '';
}

[dir=ltr] .breadcrumb__item-link:before {
  left: 6.06217px;
  right: 6.06217px;
  transform: skewX(-30deg);
}

[dir=rtl] .breadcrumb__item-link:before {
  right: 6.06217px;
  left: 6.06217px;
  transform: skewX(30deg);
}

[dir=ltr] .breadcrumb__item--first .breadcrumb__item-link {
  padding-left: 7px;
}

[dir=rtl] .breadcrumb__item--first .breadcrumb__item-link {
  padding-right: 7px;
}

.breadcrumb__item--first .breadcrumb__item-link:after {
  content: '';
}

[dir=ltr] .breadcrumb__item--first .breadcrumb__item-link:after {
  left: 0;
  right: 12.12435px;
}

[dir=rtl] .breadcrumb__item--first .breadcrumb__item-link:after {
  right: 0;
  left: 12.12435px;
}

.breadcrumb__item--parent .breadcrumb__item-link:hover:before, .breadcrumb__item--parent .breadcrumb__item-link:hover:after {
  background-color: #e5e5e5;
}

.breadcrumb__item--parent .breadcrumb__item-link:active:before, .breadcrumb__item--parent .breadcrumb__item-link:active:after {
  transition-duration: 0s;
  background-color: #e0e0e0;
}

[dir=ltr] .breadcrumb__item--last {
  margin-right: 0;
}

[dir=rtl] .breadcrumb__item--last {
  margin-left: 0;
}

.breadcrumb__title-safe-area {
  display: block;
  height: 21px;
  width: calc(50% + var(--block-header-title-width, 0px) / 2 + 64px);
  margin-top: 4px;
}

[dir=ltr] .breadcrumb__title-safe-area {
  float: left;
}

[dir=rtl] .breadcrumb__title-safe-area {
  float: right;
}

@media (max-width: 1199.98px) {
  .breadcrumb__title-safe-area {
    display: none;
  }
}

.breadcrumb__spaceship-safe-area {
  display: block;
  height: 21px;
  width: calc(50% + 480px / 2 + 20px);
  margin-top: 4px;
}

[dir=ltr] .breadcrumb__spaceship-safe-area {
  float: right;
}

[dir=rtl] .breadcrumb__spaceship-safe-area {
  float: left;
}

@media (min-width: 1200px) and (max-width: 1399.98px) {
  .breadcrumb__spaceship-safe-area {
    width: calc(50% + 440px / 2 + 20px);
  }
}

@media (max-width: 1199.98px) {
  .breadcrumb__spaceship-safe-area {
    display: none;
  }
}

/*
// .btn
*/
.btn {
  border: none;
  border-radius: 2px;
  fill: currentColor;
  font-weight: 500;
}

.btn.disabled, .btn:disabled {
  cursor: default;
}

.btn-icon {
  padding: 0;
}

.btn-icon svg {
  display: block;
  margin: auto;
}

.btn-loading {
  position: relative;
}

.btn-loading:after {
  /* nl size (default) */
  border-style: solid;
  animation-name: loader-animation;
  animation-duration: .5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  display: block;
  content: '';
  border-width: 2px;
  left: calc(50% - 12px);
  top: calc(50% - 12px);
  width: 24px;
  height: 24px;
  border-radius: 12px;
}

.btn-loading.btn-xl:after {
  /* xl size */
  left: calc(50% - 21px);
  top: calc(50% - 21px);
  width: 42px;
  height: 42px;
  border-radius: 21px;
}

.btn-loading.btn-lg:after {
  /* lg size */
  left: calc(50% - 16px);
  top: calc(50% - 16px);
  width: 32px;
  height: 32px;
  border-radius: 16px;
}

.btn-loading.btn-sm:after {
  /* sm size */
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  width: 20px;
  height: 20px;
  border-radius: 10px;
}

.btn-loading.btn-xs:after {
  /* xs size */
  left: calc(50% - 8px);
  top: calc(50% - 8px);
  width: 16px;
  height: 16px;
  border-radius: 8px;
}

.btn:not(.btn-icon) {
  font-size: 16px;
  line-height: 19px;
  padding: calc(17px / 2) 16px;
}

.btn.btn-icon {
  height: 36px;
  width: 36px;
}

.btn-sm:not(.btn-icon) {
  font-size: 14px;
  line-height: 17px;
  padding: calc(13px / 2) 12px;
}

.btn-sm.btn-icon {
  height: 30px;
  width: 30px;
}

.btn-xs:not(.btn-icon) {
  font-size: 13px;
  line-height: 15px;
  padding: calc(9px / 2) 9px;
}

.btn-xs.btn-icon {
  height: 24px;
  width: 24px;
}

.btn-lg:not(.btn-icon) {
  font-size: 18px;
  line-height: 22px;
  padding: calc(20px / 2) 24px;
}

.btn-lg.btn-icon {
  height: 42px;
  width: 42px;
}

.btn-xl:not(.btn-icon) {
  font-size: 20px;
  line-height: 26px;
  padding: calc(34px / 2) 40px;
}

.btn-xl.btn-icon {
  height: 60px;
  width: 60px;
}

.btn-primary:hover {
