  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  transition: opacity .3s, visibility 0s .3s;
}

.block-products-carousel__carousel-loader:before {
  position: absolute;
  display: block;
  content: '';
  left: -10px;
  top: -10px;
  width: calc(100% + 20px);
  height: calc(100% + 20px);
  background: #fafafa;
  opacity: .9;
