/*
// .post-header
*/
.post-header {
  position: relative;
  display: flex;
  align-items: center;
  z-index: 0;
  overflow: hidden;
}

.post-header__decor {
  position: absolute;
  bottom: -1px;
}

.post-header__decor .decor__start,
.post-header__decor .decor__end,
.post-header__decor .decor__center {
  background: #fafafa;
}

.post-header__decor .decor__start,
.post-header__decor .decor__end {
  box-shadow: none;
}

.post-header__image {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-size: cover;
  background-position: center center;
  z-index: -1;
  opacity: .22;
}

.post-header__body {
  max-width: 620px;
  margin: 0 auto;
  text-align: center;
  padding: 52px 30px;
}

.post-header__title {
  margin: 0;
  font-size: 36px;
  font-weight: 700;
}

.post-header__categories {
  margin-bottom: 28px;
}

.post-header__categories-list {
  list-style: none;
  margin: -3px;
  padding: 0;
  display: flex;
  justify-content: center;
}

.post-header__categories-item {
  margin: 3px;
}

.post-header__categories-link {
  display: block;
  font-size: 10px;
  text-transform: uppercase;
  background: #006847;
  padding: 3px 9px;
  border-radius: 1.5px;
  font-weight: 500;
}

@media (max-resolution: 1dppx) {
  .post-header__categories-link {
    font-weight: 400;
  }
}

.post-header__categories-link, .post-header__categories-link:hover {
  color: #fff;
}

.post-header__meta {
  font-size: 14px;
  margin-top: 24px;
  color: #999;
}

.post-header__meta-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.post-header__meta-item + .post-header__meta-item {
  position: relative;
}

[dir=ltr] .post-header__meta-item + .post-header__meta-item {
  margin-left: 24px;
}

[dir=rtl] .post-header__meta-item + .post-header__meta-item {
  margin-right: 24px;
}

.post-header__meta-item + .post-header__meta-item:before {
  display: block;
  position: absolute;
  content: '';
  width: 4px;
  height: 4px;
  border-radius: 2px;
  background: currentColor;
  top: 9px;
}

[dir=ltr] .post-header__meta-item + .post-header__meta-item:before {
  left: -14px;
}

[dir=rtl] .post-header__meta-item + .post-header__meta-item:before {
  right: -14px;
}

.post-header__meta-link {
  color: inherit;
  transition: color .1s;
}

.post-header__meta-link:hover {
  color: #003f2b;
}

